.ant-modal-header {
    border: none !important;
}

.buy-btn {
    font-weight: 600;
    font-size  : 15px;
    color      : #6080b4 !important;

    span {
        color: #6080b4 !important;

        &:hover {
            color: #6080b4 !important;
        }
    }
}

.buy-btn:hover {
    background: none !important;
}

.buy-big-btn {
    width        : 140px;
    height       : 42px;
    line-height  : 42px;
    background   : #6080b4;
    border-radius: 10px;
    color        : #fff;
    text-align   : center;
    cursor       : pointer;
}

.ant-btn-text:hover {
    background: #6080b4;
    color     : #fff !important;
}

.name-style {
    word-break : break-all;
    white-space: normal;
}